// UI link
var uiLink = $('.ui_link');
var uiLinkHeight = uiLink.innerHeight();
var uiLinkOffset = uiLink.offset().top;
var fixPadding = 10;

uiLink.find('a').on('click', function(){
    var uiId = $(this).text();
    var uiIdTop =  $('#ui'+uiId).offset().top - uiLinkHeight - fixPadding;

    // before fix
    if(!$(this).parent().hasClass('fix')){
        uiIdTop =  uiIdTop - uiLinkHeight;
    } else {
        uiIdTop =  $('#ui'+uiId).offset().top - uiLinkHeight + fixPadding;
    }

    $('html,body').stop().animate({scrollTop:uiIdTop});
});

// Go Top
var goTop = $('.go_top');
var _window = $(window);
var winSc = _window.scrollTop();
var isTop = true;
var isFix = false;

goTop.on('click', function(){
    $('html,body').stop().animate({scrollTop:0});
});

// scroll
_window.on('scroll', function(){
    winSc = _window.scrollTop();

    // goTop
    if(!isTop){
        if(winSc == 0){
            isTop = true;
            goTop.stop().fadeOut();
        } 
    } else{
        if(winSc > 0){
            goTopShow();
        } 
    }

    // fixLink
    if(!isFix){
        if(winSc >= uiLinkOffset){
            isFix = true;
            uiLink.addClass('fix');
        } 
    } else{
        if(winSc < uiLinkOffset){
            isFix = false;
            uiLink.removeClass('fix');
        } 
    }

});

if(winSc !== 0){
    goTopShow();
}

function goTopShow(){
    isTop = false;
    goTop.stop().fadeIn();
}


